import { useContext } from "react";
import { TaskContext } from "../context/task";
import { NavLink } from "react-router-dom";
import { TITLE_TASK, DESC_TASK } from "../types/tasks";

const TaskDetails = () => {
  const [taskState, dispatch] = useContext(TaskContext);
  const { tasks } = taskState;
  const taskIdUrl = window.location.pathname.split("/")[3];
  const task = tasks.filter((t) => +t.id === +taskIdUrl);

  const createTitle = (e) => {
    e.preventDefault();
    dispatch({
      type: TITLE_TASK,
      payload: { id: +taskIdUrl, realTitle: e.target[0].value, task },
    });
  };

  const createDesc = (e) => {
    e.preventDefault();
    console.log(e.target[0].value);
    dispatch({
      type: DESC_TASK,
      payload: { id: +taskIdUrl, description: e.target[0].value, task },
    });
  };

  return (
    <div className="container col-lg-8 offset-lg-1">
      <NavLink to="/" className="btn btn_link">
        Revenir à la liste
      </NavLink>
      <h1>{task.title}</h1>
      <form onSubmit={createTitle}>
        <div className="form-group">
          <label className="col-form-label" htmlFor="inputDefault">
            Titre
          </label>
          <input
            type="text"
            className="form-control"
            placeholder="Titre..."
            id="inputDefault"
          />
        </div>
        <button type="submit" className="btn btn-secondary">
          Sauvegarder
        </button>
      </form>

      <form onSubmit={createDesc}>
        <div className="form-group mt-5">
          <label htmlFor="exampleTextarea">Description</label>
          <textarea
            className="form-control"
            id="exampleTextarea"
            rows="3"
            placeholder="Petite description..."
          ></textarea>
        </div>
        <button type="submit" className="btn btn-secondary">
          Sauvegarder
        </button>
      </form>
    </div>
  );
};

export default TaskDetails;
